
import User   from './user';
import Issues from './issues';
import Issue  from './issue';

async function getUser() {
    try {
        const user    = new User();
        const respone = await user.fetch( user.query() );
        const result  = await respone.json();
        user.data     = result.data;
        return new Promise(( resolve, reject ) => {
            if ( result ) {
                resolve( user );
            } else {
                reject( result );
            }
        });
    } catch ( error ) {
        return new Promise(( resolve, reject ) => reject( error ));
    }
}

async function getIssues( page, search ) {
    try {
        const issues  = new Issues();
        const respone = await issues.fetch( issues.query( page, search ));
        const result  = await respone.json();
        issues.data   = result.data;
        return new Promise(( resolve, reject ) => {
            if ( result ) {
                resolve( issues );
            } else {
                reject( result );
            }
        });
    } catch ( error ) {
        return new Promise(( resolve, reject ) => reject( error ));
    }
}

async function getIssue( num ) {
    try {
        const issue   = new Issue();
        const respone = await issue.fetch( issue.query( num ));
        const result  = await respone.json();
        issue.data    = result.data;
        return new Promise(( resolve, reject ) => {
            if ( result ) {
                resolve( issue );
            } else {
                reject( result );
            }
        });
    } catch ( error ) {
        return new Promise(( resolve, reject ) => reject( error ));
    }
}

module.exports = {
    getUser, getIssues, getIssue
};